import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import { BtnSave, BtnCancel } from 'components/common/button';
import { Card, Form, Input, Checkbox, Upload, Button, Icon } from 'antd';
import DatePickerString from 'components/common/input/DatePickerString';
import { getDocInId, createDocIn, updateDocIn } from 'services/docIn';
import moment from 'moment';
import { getQueryParams } from 'utils/urlUtil';

import { requiredRule } from 'utils/validateRules';

const formatDate = 'YYYY-MM-DD';
const initialValueDefault = {
  code: '',
  esId: '',
  firstname: '',
  lastname: '',
  dob: '',
  pobId: '',
  gender: '',
  schoolId: '',
  areaId: '',
  graduatedYear: '',
  studyField: '',
  testGroup: '',
  gradeM1: '',
  gradeM2: '',
  gradeM3: '',
  certType: '',
  trainTypeId: '',
  isSentEmail: '',
};

@Form.create({ name: 'CandidateStudentFormPage' })
class CandidateStudentFormPage extends Component {
  state = {
    dataId: false,
  };

  componentDidMount = async () => {
    const { form } = this.props;
    const { id } = getQueryParams();
    if (id) {
      this.setState({ dataId: id });
      const initialValue = await this.getDataById(id);
      form.setFieldsValue(initialValue);
    } else {
      form.setFieldsValue(initialValueDefault);
      this.props.form.setFieldsValue({
        docInDate: moment(new Date()),
      });
      this.props.form.setFieldsValue({
        noteDate: moment(new Date()),
      });
    }
  };

  getDataById = async id => {
    const res = await getDocInId(id);
    return { ...initialValueDefault, ...res.data };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (err) {
        return;
      }
      this.saveData(values);
    });
  };

  normFile = e => {
    if (Array.isArray(e)) {
      return e;
    }
    return e && e.fileList;
  };

  saveData = async doc => {
    const { dataId } = this.state;
    try {
      const formData = new FormData();
      formData.append('id', doc.id || null);
      formData.append('docInNo', doc.docInNo || '');
      formData.append('docInDate', moment(doc.docInDate).format(formatDate));
      formData.append('sentPlace', doc.sentPlace);
      formData.append('docContent', doc.docContent || '');
      formData.append('noteDate', moment(doc.noteDate).format(formatDate));
      formData.append('deadline', doc.deadline || '1900-01-01');
      formData.append('isSentEmail', doc.isSentEmail || false);
      formData.append('savedPlace', doc.savedPlace || '');
      formData.append('recipientUnit', doc.recipientUnit || '');
      formData.append('formFile', (doc.formFile && doc.formFile[0].originFileObj) || '');
      if (!dataId) {
        await createDocIn(formData);
      } else {
        await updateDocIn(formData);
      }
      this.props.history.goBack();
    } catch (error) {
      console.log(error);
    }
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { dataId } = this.state;
    return (
      <Card title="Thêm ứng viên">
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Form.Item label="Số CV đến">
            {getFieldDecorator('docInNo', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Đến ngày">
            {getFieldDecorator('docInDate', { rules: [requiredRule] })(
              <DatePickerString placeholder="Chọn ngày" />
            )}
          </Form.Item>
          <Form.Item label="Nơi gửi">
            {getFieldDecorator('sentPlace', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Số vào sổ">
            {getFieldDecorator('id', { rules: [requiredRule] })(<Input disabled={dataId} />)}
          </Form.Item>
          <Form.Item label="Ngày vào">
            {getFieldDecorator('noteDate')(<DatePickerString placeholder="Chọn ngày" />)}
          </Form.Item>
          <Form.Item label="Nội dung">
            {getFieldDecorator('docContent', { rules: [requiredRule] })(<Input />)}
          </Form.Item>
          <Form.Item label="Hạn sử lý">
            {getFieldDecorator('deadline')(<DatePickerString placeholder="Chọn ngày" />)}
          </Form.Item>
          <Form.Item label="Nơi lưu">{getFieldDecorator('savedPlace')(<Input />)}</Form.Item>
          <Form.Item label="Đã gửi mail">
            {getFieldDecorator('isSentEmail')(<Checkbox />)}
          </Form.Item>
          <Form.Item label="ĐV ký nhận">{getFieldDecorator('recipientUnit')(<Input />)}</Form.Item>
          <Form.Item label="Upload File">
            {getFieldDecorator('formFile', {
              valuePropName: 'fileList',
              getValueFromEvent: this.normFile,
            })(
              <Upload
                action="/upload.do"
                name="file"
                listType="text"
                onRemove={this.handleRemoveFile}
                beforeUpload={() => false}
              >
                <Button>
                  <Icon type="upload" style={{ width: 5 }} /> Upload
                </Button>
              </Upload>
            )}
          </Form.Item>
          <div style={{ textAlign: 'center' }}>
            <BtnSave htmlType="submit" />
            <BtnCancel onClick={() => this.props.history.goBack()} />
          </div>
        </Form>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <CandidateStudentFormPage {...props} />
  </Page>
));



