import { request, requestDownload } from 'utils';

const prefix = '/api/organization';

export const getDocIns = ({
  id,
  docInNo,
  docInDate,
  sentPlace,
  noteDate,
  docContent,
  savedPlace,
  recipientUnit,
  isCompleted,
  page,
  pageSize = 10,
  orderBy,
  isPagingEnabled = true,
} = {}) =>
  request({
    prefix,
    url: '/DocIn',
    method: 'GET',
    params: {
      id,
      docInNo,
      docInDate,
      sentPlace,
      noteDate,
      docContent,
      savedPlace,
      recipientUnit,
      isCompleted,
      page,
      pageSize,
      orderBy,
      isPagingEnabled,
    },
    data: {},
  });

export const createDocIn = newDoc =>
  request({
    prefix,
    url: `/DocIn`,
    data: newDoc,
    method: 'POST',
  });

export const deleteDocIn = id =>
  request({
    prefix,
    url: `/DocIn`,
    params: { id },
    method: 'DELETE',
  });

export const getDocInId = id =>
  request({
    prefix,
    url: `/DocIn/GetById`,
    params: { id },
    method: 'GET',
  });

export const updateDocIn = editDoc =>
  request({
    prefix,
    url: `/DocIn`,
    data: editDoc,
    method: 'PUT',
  });

export const downloadFile = paramsSearch =>
  requestDownload({
    prefix,
    defaultFileName: 'ExportDocIns.xlsx',
    url: `/DocIn/ExportDocIns`,
    data: { ...paramsSearch },
    method: 'POST',
  });



